.form {
  display: flex;
  position: fixed;
  top: 195px;
  right: calc(50% - 215px);
  flex-direction: column;
  align-items: center;
  column-gap: 8px;
  margin: 0 auto;
  max-width: 430px;
}
.form__title {
  color: #000000;
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  max-width: 330px;
  width: 100%;
  text-align: left;
  align-self: center;
  margin: 34px auto 24px;
}

.form__fieldset {
  display: inline-block;
  max-width: 430px;
  width: 100%;
  max-height: 330px;
  margin: 0 auto;
  border-style: none;
  padding: 0;
}

.form__input {
  display: block;
  position: relative;
  max-width: 358px;
  width: 100%;
  height: 26px;
  margin: 30px auto 0;
  border-style: none;
  border-bottom: 1px solid #000000;
  padding: 0;
}
.form__button {
  display: block;
  position: relative;
  box-sizing: border-box;
  border-radius: 4px;
  background-color: #000;
  max-width: 358px;
  height: 46px;
  width: 100%;
  margin: 48px auto 36px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  padding: 0;
}

.form__button:hover {
  opacity: 70%;
}

.form__input_type_error {
  border-bottom: 1px solid #f00;
}

.form__error-visible {
  display: block;
  position: absolute;
  color: #ff0000;
  margin-left: 35px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  text-align: justify;
  max-width: 358px;
}
.form__button_disabled {
  box-sizing: border-box;
  opacity: 0.2;
  border: 1px solid #000000;
  border-radius: 2px;
  user-select: none;
  pointer-events: none;
}

@media screen and (min-width: 320px) and (max-width: 1024px) {
  .form {
    top: 123px;
    right: calc(50% - 141px);
    max-width: 282px;
    margin: 0;
  }
  .form__title {
    font-weight: 900;
    font-size: 18px;
    line-height: 22px;
    max-width: 238px;
    margin: 25px auto 46px;
  }
  .form__fieldset {
    max-width: 238px;
  }
  .form__input {
    max-width: 238px;
  }
  .form__error-visible {
    margin-left: 0;
    max-width: 338px;
  }

  .form__button {
    max-width: 238px;
    height: 46px;
    font-size: 14px;
    line-height: 17px;
    margin: 31px auto 25px;
  }
}
